import { gergMonths, weekDays, weekDaysMin } from '../common/months';

export const moment = require('moment-hijri');
moment.updateLocale('ar-sa', {
	// customizations.
	months: gergMonths,
	monthsShort: gergMonths,
	weekdays: weekDays,
	weekdaysShort: weekDaysMin,
	weekdaysMin: weekDaysMin,
	relativeTime: {
		future: ' %s',
		past: ' %s',
		s: '',
		ss: function (number, withoutSuffix, key, isFuture) {
			return number > 10
				? number + ' '
				: number > 2
				? number + ' '
				: number === 2
				? ''
				: '';
		},
		m: '',
		mm: function (number, withoutSuffix, key, isFuture) {
			return number > 10
				? number + ' '
				: number > 2
				? number + ' '
				: number === 2
				? ''
				: '';
		},
		h: '',
		hh: function (number, withoutSuffix, key, isFuture) {
			return number > 10
				? number + ' '
				: number > 2
				? number + ' '
				: number === 2
				? ''
				: '';
		},
		d: '',
		dd: function (number, withoutSuffix, key, isFuture) {
			return number > 10
				? number + ' '
				: number > 2
				? number + ' '
				: number === 2
				? ''
				: '';
		},
		w: '',
		ww: function (number, withoutSuffix, key, isFuture) {
			return number > 10
				? number + ' '
				: number > 2
				? number + ' '
				: number === 2
				? ''
				: '';
		},
		M: '',
		MM: function (number, withoutSuffix, key, isFuture) {
			return number > 10
				? number + ' '
				: number > 2
				? number + ' '
				: number === 2
				? ''
				: '';
		},
		y: '',
		yy: function (number, withoutSuffix, key, isFuture) {
			return number > 10
				? number + ' '
				: number > 2
				? number + ' '
				: number === 2
				? ''
				: '';
		},
	},
	meridiem: function (hour, minute, isLowercase) {
		if (hour < 12) {
			return '';
		} else {
			return '';
		}
	},
	calendar: {
		lastDay: '[ ] LT',
		sameDay: '[ ] LT',
		nextDay: '[ ] LT',
		lastWeek: 'dddd [ ] LT',
		nextWeek: 'dddd [] LT',
		sameElse: 'L',
	},
	week: {
		dow: 0, // Sunday is the first day of the week.
		doy: 6, // The week that contains Jan 1st is the first week of the year.
	},
});
